.search-form {
  display: flex;
  gap: 10px;
  color: white;
  font-size: 24px;
  height: 100%;
  .form-item {
    background-color: transparent;
    padding: 10px;
    font-size: 24px;
    outline: none;
    border: 2px solid white;
    color: white;
    font-weight: 500;
  }
  .form-input {
    width: 400px;
    border-radius: 8px 0 0 8px;
  }
  .form-input::placeholder {
    color: rgb(187, 183, 183);
  }
  .search-btn {
    border-radius: 0 8px 8px 0;
    cursor: pointer;
    display: flex;
    align-items: center;
    font-weight: normal;
  }
}
